.alignwide {
  @include breakpoint(xxhuge) {
    padding-left: min(3.5rem, 2vw) !important;
    padding-right: min(3.5rem, 2vw) !important;
  }
}

@media (min-width: 1400px) and (max-width: 1600px) {
  .alignwide {
    padding-left: min(3.5rem, 2vw) !important;
    padding-right: min(3.5rem, 2vw) !important;
  }
}
